import React from "react";
import { Link } from "react-router-dom";

const NavBar = () => {
  return (
    <nav>
      <ul>
        <li>
          <Link to="/">Home Sweet Home</Link>
        </li>
        <li>
          <Link to="/about">about</Link>
        </li>

        <li>
          <Link to="/articles">articleList</Link>
        </li>

        <li>
          <Link to="/article">article</Link>
        </li>
      </ul>
    </nav>
  );
};

export default NavBar;
